.box {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: calc(100vh - 100vw * 0.21);
}

.inner {
  flex-grow: 1;
}

.button {
  width: 100%;
  border-radius: 5px;
  background: #3EB98C;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FDFDFD;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-weight: 600;
  padding: 8px 0;
  border: none;
  outline: none;
}

.button:disabled {
  border-radius: 5px;
  background: #E5E5E5;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
  color: #727070;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 3px;
}